@include font-face(Montserrat, fonts/Montserrat/Montserrat-ExtraBold);
@include font-face(Roboto, fonts/Roboto/Roboto-Medium);

h1,h2,h3,h4,h5,h6 {
  font-family: "Montserrat";
  margin: 0;
  padding: 0.5em 0;
}
h1 {
  font-size: 1.5em;
  text-align: left;
  font-weight: bold;
  padding:0;
}

p {
  font-size: 0.7em;
  text-align: left;
  font-weight: 100;
}
