@media screen and (min-width:480px){
  body {
    background: red;
  }
  .hero .form-heading form {
    width: 420px;
}
}

@media screen and (min-width:768px){
  body {
    background: blue;
  }
  .hero {
    height: 650px;
}
  .hero .text-heading {
    grid-area: 1 / span 6;
}
.hero-animation {
  grid-area: 1 / span 1;
}
.form-heading {
  grid-area: 1 / span 5;
}
.hero .row {
  grid-auto-rows: 100%;
}
.hero-animation .curveDownColor {
  height: 200%;
  top: -5%;
  left: -15%;
  width: 100%;
  transform: translate3d(-5%, 0%, 0);
}
.hero-animation .curveDownColor path {
  fill: #1b1b1b;
  d: path("M 0 700 C 70 350 70 140 0 0 L 0 700 Z");
}
.hero-animation .curveDownColor:nth-of-type(2) {
  top: -4%;
  left: -5%;
  animation-duration: 2s;
}
